<template>
    <div class="headertop">
        <div class="header" :style="bgStyle">
        <router-link tag="div" to="/" class="input" :style="inputStyle">
            <span class="icon-bg"><van-icon name="search" /></span>
            <span class="input-title">121活动套餐大优惠</span>
        </router-link>
            <div class="icon-right" :style="iconStyle"><van-icon name="cart-o" /></div>
        </div>
    </div>
</template>

<script>
import { Icon } from 'vant';
import 'vant/lib/icon/local.css';
export default {
    name:'homeHader',
    components:{
        [Icon.name]:Icon
    },
    data () {
        return {
            bgStyle:{},
            inputStyle:{},
            iconStyle:{}
        }
    },
    methods:{
        handleScroll () {
            const top = document.documentElement.scrollTop
            if(top>60){
                this.bgStyle={
                    background:'#fff',
                    'border-bottom': '1px solid rgba(160,160,160,.6)'
                }
                this.inputStyle = {
                    background:'rgba(0,0,0,.05)'
                }
                this.iconStyle = {
                    color : '#ccc'
                }
            }else{
                  this.bgStyle={
                    background:'',
                    'border-bottom': ''
                }
                this.inputStyle.background = ''
                this.iconStyle = {
                    color : '#fff'
                }
            }
        }
    },
    created () {
        window.addEventListener('scroll',this.handleScroll)
    },
    deactivated () {
        window.removeEventListener('scroll',this.handleScroll)
    }
} 
</script> 
<style lang="less" scoped>
@import '~styles/theme.less';
.headertop{
    padding-top: @headerHeight;
}
.header{
    display:flex;
    line-height:@headerHeight;
    height: @headerHeight;
    background-color:@bgColor;
    color:#fff;
    padding: 0 .24rem ;
    align-items: center;
    justify-content: space-between;
    z-index:99;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}
.icon-right{
    display: flex;
    height: 1rem;
    padding-left: .2rem;
    font-size: .45rem;
    align-items: center;
}
.input{
    width: 90%;
    height:.60rem;
    flex :1;
    overflow: hidden;
    background-color: #fff;
    border-radius: 4px;
    font-size: .3rem;
    display: flex;
    
}
.input-title{
    padding-left: .1rem;
    line-height: .60rem;
    color: #808080;
}
.icon-bg{
    padding-left: .2rem;
    line-height: .68rem;
    font-size: .32rem;
    color: #808080;
}
</style>




