<template>
  <div>
    <mask-load :roll="isRollLoading"></mask-load>
    <home-herder></home-herder>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <home-banner :list="bannerList"></home-banner>
      <home-boutique :list="boutiqueList"></home-boutique>
      <recommended-slider></recommended-slider>
      <home-clumn></home-clumn>
    </van-pull-refresh>
    <tabbar></tabbar>
  </div>
</template>

<script>

import homeHerder from './components/header'
import homeBanner from './components/banner'
import homeBoutique from './components/boutique'
import recommendedSlider from './components/recommendedSlider'
import homeClumn from './components/column'
import Tabbar from 'common/tabbar'
import maskLoad from 'common/maskLoad'
import { PullRefresh,Toast } from 'vant'
import { apiBanner,apiBoutique,apiSpecial } from '@/request/api'

export default {
    name:'home',
    components:{
      homeHerder,
      homeBanner,
      homeBoutique,
      recommendedSlider,
      homeClumn,
      Tabbar,
      [PullRefresh.name]:PullRefresh,
      [Toast.name]:Toast,
      maskLoad
    },
     data() {
      return {
        count: 0,
        isLoading: false,
        baseUrl:this.GLOBAL.baseURL,
        bannerList:[],
        boutiqueList:[],
        isRollLoading:false
      }
  },
  created () {
    this.onLoad();
  },
  methods: {
    onRefresh() {
      setTimeout(() => {
        this.$toast({
          message:'刷新成功',
          duration:'1000'
        });
        this.isLoading = false;
        // this.count++;
      }, 1000);
    },
    onLoad () {
      apiBanner({
      }).then(res=>{
        this.isRollLoading = true
        this.bannerList = res
      })
      apiBoutique({
      }).then(res => {
        this.boutiqueList = res
      })
      apiSpecial({
      }).then(res => {
        
      })
    }
  
  },
  mounted () {
    //模拟加载
    // setTimeout(()=>{
    //     this.isRollLoading = true
    // },1500)
  }
}
</script>

<style lang="less" scoped>

</style>




