<template>
     <div class="load-v" :style="styleObject" :roll="roll" v-show="roll === false" @touchmove.prevent>
        <van-loading color="#000" class="load"/>
      </div>
</template>

<script>
import { Loading } from 'vant'

export default {
    name:'maskLoad',
    components:{
        [Loading.name]:Loading
    },
    props:{
        roll:{
            type:Boolean,
            default:false
        }
    },
    data () {
        return {
            styleObject:{
                height:null
            }
        }
    },
    watch:{
        roll (val) {
            console.log(val)
        }
    }
    ,
    methods : {
        // overflowr () {
        //     if(this.roll === false){
        //         document.documentElement.style.overflowY = 'hidden'
        //     }else{
        //         document.documentElement.style.overflowY = 'visible'
        //     }
        // }
    },
    mounted () {
        let h = window.innerHeight
        this.styleObject.height = h + 'px'
        // this.overflowr()
    }
}
</script>

<style lang="less" scoped>
.load-v{
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(255, 255, 255 ,.1);
}
</style>



