<template>
    <div class="tabbar-bottom">
        <div class="footer">—— 到底啦 ——</div>
        <van-tabbar v-model="active">
             <van-tabbar-item icon="wap-home">首页</van-tabbar-item>
             <van-tabbar-item icon="discount" >秒杀</van-tabbar-item>
             <van-tabbar-item icon="points-mall">找好物</van-tabbar-item>
             <van-tabbar-item icon="cart">购物车</van-tabbar-item>
             <van-tabbar-item icon="contact">我的</van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
import { Tabbar, TabbarItem } from 'vant';

export default {
    name:'tabbar',
    components:{
        [Tabbar.name]:Tabbar,
        [TabbarItem.name]:TabbarItem
    },
    data() {
    return {
      active: 0
    }
  }
}
</script>

<style lang="less" scoped>
.tabbar-bottom{
    padding-bottom: 1.6rem;
}
.footer{
    font-size: .28rem;
    color: #a0a0a0;
    text-align: center;
    padding-top: .4rem;
}
</style>



