<template>
    <div class="banner">
        <van-swipe :autoplay="3000" indicator-color="white">
            <van-swipe-item v-for="item of list" :key="item.id">
                <router-link tag="div" class="item" to="/">
                     <img class="item-img" :src="item.img[0].img_url" :alt="item.title">
                </router-link>
            </van-swipe-item>
        </van-swipe>
    </div>
</template>

<script>
import { Swipe, SwipeItem } from 'vant'

export default {
    name : 'homeBanner',
    props:{
        list:Array
    },
    components:{
        [Swipe.name]:Swipe,
        [SwipeItem.name]:SwipeItem
    }
}

</script>

<style lang="less" scoped>
@import '~styles/theme.less';
.banner{
    height: 3.2rem;
}
.item{
    height: 3.2rem;
    background-color: antiquewhite
}
.item-img{
    width: 100%;
    height: auto;
}
</style>



