<template>
    <div class="boutique">
        <van-row class="row" type="flex" justify="space-between" align="bottom">
            <van-col class="row-li" span="12" v-for="item of list" :key="item.id">
                <img class="row-li-img" :src="item.img[0].img_url" :alt="item.title">
            </van-col>
        </van-row>
    </div>
</template>

<script>
import { Row, Col } from 'vant'

export default {
    name:'homeBoutique',
    props:{
        list:Array
    },
    components:{
        [Row.name]:Row,
        [Col.name]:Col
    }
}
</script>

<style lang="less" scoped>
@import '~styles/theme.less';
.boutique{
    font-size: .5rem;
    background-color: @darkTextColor;
    padding: .24rem .24rem .08rem .24rem;
    height: 3.12rem;
}
.row{
    flex-wrap: wrap;
}
.row-li{
    padding: 0 .08rem ;
    margin-bottom: .16rem;
    height: 1.4rem;
}

.row-li-img{
    width: 100%;
}
</style>



